
import { Options, Vue } from "vue-class-component";
import axios from "axios";
class Account {
  id: number | null = null;
  chipId = "";
  name = "";
  role = 1;
  beakCurvature = 0;
}

@Options({})
export default class Accounts extends Vue {
  popugs: Account[] = [];
  newPopug: Account | null = null;

  async mounted() {
    await this.getAll();
  }

  create(): void {
    this.newPopug = new Account();
  }

  async post(): Promise<void> {
    await axios.post("api/add", this.newPopug, { headers: {"X-CSRF": "1" } });
    this.newPopug = null;
  }

  reset(): void {
    this.newPopug = null;
  }

  translateRole(roleId: number) {
    switch (roleId) {
      case 1:
        return "User";
      case 10:
        return "Manager";
      case 100:
        return "Admin";
      default:
        return "Unknown";
    }
  }

  private async getAll() {
    this.popugs = (await axios.get<Account[]>("api/all", { headers: {"X-CSRF": "1" } })).data;
  }
}
